import React, { useEffect, useState } from 'react';
import Loading from './Loading.js';
import Oak from '../images/oak.png';

const Pokemon = ({match}) => {
    const { name } = match.params;
    const [firstFire, setFire] = useState(true);
    const [pokemon, setPokemon] = useState({
        species: {},
        sprites: {},
        types: []
    });
    const [query, setQuery] = useState('');
    const [types, setTypes] = useState([]);

    useEffect(() =>{
        const findPokemon = async() =>{
            if(firstFire){
                setFire(!firstFire);
                return;
            }
            const response = await fetch(`https://pokeapi.co/api/v2/pokemon/${query}`);
            const valid = response.status !== 404 ? true : false
            const data = valid ? await response.json(): false;
            setTypes(data.types);
            setPokemon(data);
        }
        findPokemon();
        setQuery(name.toLowerCase());
    }, [query, name, firstFire])

    return (
        <div className="container">
            <div className="row">
                {pokemon ? (
                    <div className="col">
                        <div className="card text-center bg-dark">
                            <div className="card-header">
                                <div className="row">
                                    <div className="col-lg-6 text-white">
                                        <h1>Name: {pokemon.name ? pokemon.name : <Loading />}</h1>
                                        <h1>Weight: {pokemon.weight ? pokemon.weight + 'kg' : <Loading />}</h1>
                                        <h1>BaseExp: {pokemon.base_experience ? pokemon.base_experience : <Loading />}</h1>
                                        <h1>Type: {types ? types.map(type => (type.type.name + ' ')) : <Loading />}</h1>
                                        <img src={Oak} alt="oak"></img>
                                    </div>
                                    <div className="col-lg-6">
                                        {pokemon.sprites.front_default ? <img src={pokemon.sprites.front_default} alt={pokemon.namw} className="img-fluid img-thumbnail bg-transparent border border-warning card-img-top"></img> : <Loading />}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ): (
                    <div>
                        <h1>Information Not Found!</h1>
                    </div>
                    )
                }
            </div>
        </div>
     );
}
 
export default Pokemon;