import React, { useState, useEffect } from 'react';
import PokemonList from './PokeList.js'

const Pokemons = (props) => {
    const [search, setSearch] = useState('');
    const [Pokemons, setPokemans] = useState({
        results: []
    });

    useEffect(() =>{
        getPokemons();
    }, [])

    const getPokemons = async() =>{
        const response = await fetch(`https://pokeapi.co/api/v2/pokemon?limit=40`);
        const data = await response.json();
        //console.log(data)
        setPokemans(data);
    }

    const updateSearch = (e) =>{
        setSearch(e.target.value);
    }

    const handleSubmit = (e) =>{
        props.history.push(`/pokemons/${search}`);
        e.preventDefault();
    }

    return ( 
        <div className="text-white">
            <div className="container bg-dark rounded">
                <h1>Search Pokemon by name or Id:</h1>
                <form onSubmit={handleSubmit} className="form-group row p-3 justify-content-center">
                    <input type="text" value={search} onChange={updateSearch} className="form-control col-3"/>
                    <button type="submit" className="btn btn-success col-2 ml-2">Search</button>
                </form>
                <p className="text-muted">Sample Input</p>
            </div>
            <PokemonList pokeList={Pokemons} />
        </div>
     );
}
 
export default Pokemons;